export default {
  props: {
    time: Number
  },
  data: function data() {
    return {
      begin_time: this.time,
      countId: null
    };
  },
  computed: {
    time_format: function time_format() {
      var begin_time = this.begin_time;
      var h = Math.floor(begin_time / 3600);
      var m = Math.floor(begin_time % 3600 / 60);
      var s = Math.floor(begin_time % 3600 % 60);
      var time_format = "\u8DDD\u79BB\u6D4B\u8BC4\u8FD8\u6709";

      if (h > 0) {
        time_format = time_format + "<span style=\"color: #FF7D30;font-size:1rem;\">".concat(h, ":").concat(m, ":").concat(s, "</span>");
      } else if (m > 0) {
        time_format = time_format + "<span style=\"color: #FF7D30;font-size:1rem;\">".concat(m, ":").concat(s, "</span>");
      } else if (s > 0) {
        time_format = time_format + "<span style=\"color: #FF7D30;font-size:1rem;\">".concat(s, "</span>");
      } else {
        time_format = " 进行中";
      }

      return time_format;
    }
  },
  methods: {
    beganCount: function beganCount() {
      var _this = this;

      var begin_time = this.begin_time;
      this.$emit("start");
      this.countId = setInterval(function () {
        _this.begin_time--;

        if (_this.begin_time === 0) {
          _this.$emit("end");

          clearInterval(_this.countId);
        }
      }, 1000);
    }
  },
  mounted: function mounted() {
    this.beganCount();
  },
  beforeDestroy: function beforeDestroy() {
    clearInterval(this.countId);
  }
};